<template>
  <div class="WeatherStation">
    <Header :inputTitle="title" class="bg-1-dark-header"></Header>
    <el-row :gutter="10">
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">風速</div>
      </el-col>
      <el-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4">
        <div class="grid-content bg-1-light" :style="[wind_speed > 10 ? alert_high : normal]">
          {{ wind_speed }}<span class="unit">m/s</span>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">雨量</div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
        <div class="grid-content bg-1-light" :style="[rain > 8 ? alert_high : normal]">
          {{ rain }}<span class="unit">mm</span>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">風向</div>
      </el-col>
      <el-col :xs="6" :sm="6" :md="6" :lg="6" :xl="6">
        <div class="grid-content bg-1-light">
          {{ wind_dir_txt }}
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="10">
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">光度</div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
        <div class="grid-content bg-1-light">
          {{ lux }}<span class="unit">lux</span>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">溫度</div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
        <div class="grid-content bg-1-light" :style="[temp > 40 ? alert_high : temp < 10 ? alert_low : normal]">
          {{ temp }}<span class="unit">°C</span>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3" :lg="3" :xl="3">
        <div class="grid-content bg-1-dark">濕度</div>
      </el-col>
      <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
        <div class="grid-content bg-1-light" :style="[humd > 90 ? alert_high : humd < 40 ? alert_low : normal]">
          {{ humd }}<span class="unit">%RH</span>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col style="font-size:0.8rem !important; height:10px; line-height:10px; text-align:right;">
        更新時間:{{ updateTime }}
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Smart_Air_Sensor, Smart_Wather_Station_Sensor } from "../api";
import Header from "@/components/MainHeader.vue";
export default {
  components: {
    Header
  },
  data() {
    return {
      alert_high: {
        color: 'red'
      },
      alert_low: {
        color: 'blue'
      },
      normal: {
        color: 'black'
      },
      title: "氣象站",
      wind_speed: 0,
      rain: 0,
      wind_dir: 0,
      lux: 0,
      temp: 0,
      humd: 0,
      getDataInterval: null,
      updateTime: "-"
    };
  },
  computed: {
    wind_dir_txt: function () {
      let txt = ''
      let value = this.wind_dir - 75
      if (value < 0) {
        value = 360 + value
      }
      if ((348.75 < value && value <= 360) || (0 <= value && value <= 11.25)) {
        txt = "北"
      } else if (11.25 < value && value <= 33.75) {
        txt = "北北東"
      } else if (33.75 < value && value <= 56.25) {
        txt = "東北"
      } else if (56.25 < value && value <= 78.75) {
        txt = "東北東"
      } else if (78.75 < value && value <= 101.25) {
        txt = "東"
      } else if (101.25 < value && value <= 123.75) {
        txt = "東南東"
      } else if (123.75 < value && value <= 146.25) {
        txt = "東南"
      } else if (146.25 < value && value <= 168.75) {
        txt = "南南東"
      } else if (168.75 < value && value <= 191.25) {
        txt = "南"
      } else if (191.25 < value && value <= 213.75) {
        txt = "南南西"
      } else if (213.75 < value && value <= 236.25) {
        txt = "西南"
      } else if (236.25 < value && value <= 258.75) {
        txt = "西南西"
      } else if (258.75 < value && value <= 281.25) {
        txt = "西"
      } else if (281.25 < value && value <= 303.75) {
        txt = "西北西"
      } else if (303.75 < value && value <= 326.25) {
        txt = "西北"
      } else if (326.25 < value && value <= 348.75) {
        txt = "北北西"
      } else {
        txt = "暫無提供"
      }
      return txt
    }
  },
  methods: {
    getTempRhData() {
      Promise.all([
        Smart_Air_Sensor('智慧溫室'),
        Smart_Wather_Station_Sensor()
      ]).then(([airData, weatherData]) => {
        this.temp = airData.data[1].TEMP;
        this.humd = airData.data[1].HUMD;
        this.lux = weatherData.data[0].LUX;
        this.wind_speed = weatherData.data[0].WIND_SPEED;
        this.wind_dir = weatherData.data[0].WIND_ROSE;
        this.rain = weatherData.data[0].RAIN;
        this.updateTime = new Date().toLocaleString('sv');
      })
        .catch(e => {
          console.log(e);
        });
    }
  },
  watch: {},
  mounted() {
    /* 更新溫溼度折線圖 */
    clearInterval(this.getDataInterval);
    this.getTempRhData();
    this.getDataInterval = setInterval(this.getTempRhData, 300 * 1000);
  },
  beforeUnmount() {
    clearInterval(this.getDataInterval);
  }
};
</script>

<style scoped src="../assets/css/compenents.css"></style>