import axios from "axios";

const apiClient = axios.create({
    baseURL: 'https://yuan-tech.co/api',
    headers: {
        'Content-Type': 'application/json',
    },
});

export const Smart_Air_Sensor = (a) => {
    return apiClient.get('/Smart_Air_Sensor', {
        params: {
            FIELD_ID: a,
        },
    });
};
export const Smart_PM_Sensor = () => {
    return apiClient.get('/Smart_PM_Sensor', {
    });
};
export const Smart_Soil_Sensor = (a) => {
    return apiClient.get('/Smart_Soil_Sensor', {
        params: {
            FIELD_ID: a,
        },
    });
};
export const Smart_Wather_Station_Sensor = () => {
    return apiClient.get('/Smart_Wather_Station_Sensor', {
    });
};