<script>
import { Smart_Air_Sensor, Smart_Soil_Sensor, Smart_PM_Sensor } from "../api";
import Header from "@/components/MainHeader.vue";
export default {
  components: {
    Header
  },
  data() {
    return {
      alert_high: {
        color: 'red'
      },
      alert_low: {
        color: 'blue'
      },
      normal: {
        color: 'black'
      },
      title: "智慧溫室",
      pm_sensor: {
        title: '空氣',
        pm25: 0,
        pm10: 0
      },
      air_sensor_1: {
        title: '溫濕光1',
        temp: 0,
        humd: 0,
        lux: 0
      },
      air_sensor_2: {
        title: '溫濕光2',
        temp: 0,
        humd: 0,
        lux: 0
      },
      soil_sensor_1: {
        title: '土壤1',
        temp: 0,
        humd: 0,
        ec: 0,
        ph: 0
      },
      soil_sensor_2: {
        title: '土壤2',
        temp: 0,
        humd: 0,
        ec: 0,
        ph: 0
      },
      getDataInterval: null,
      updateTime: "-"
    };
  },
  computed: {},
  methods: {
    getTempRhData() {
      Promise.all([
        Smart_Air_Sensor('智慧溫室'),
        Smart_Soil_Sensor('智慧溫室'),
        Smart_PM_Sensor()
      ]).then(([airData, soilData, pmData]) => {
        this.air_sensor_1.temp = airData.data[0].TEMP;
        this.air_sensor_1.humd = airData.data[0].HUMD;
        this.air_sensor_1.lux = airData.data[0].LUX;
        this.air_sensor_2.temp = airData.data[1].TEMP;
        this.air_sensor_2.humd = airData.data[1].HUMD;
        this.air_sensor_2.lux = airData.data[1].LUX;
        this.soil_sensor_1.temp = soilData.data[0].TEMP * 10;
        this.soil_sensor_1.humd = soilData.data[0].MIOS * 10;
        this.soil_sensor_1.ec = soilData.data[0].EC * 10;
        this.soil_sensor_1.ph = soilData.data[0].PH * 10;
        this.soil_sensor_2.temp = soilData.data[1].TEMP * 10;
        this.soil_sensor_2.humd = soilData.data[1].MIOS * 10;
        this.soil_sensor_2.ec = soilData.data[1].EC * 10;
        this.soil_sensor_2.ph = soilData.data[1].PH * 10;
        this.pm_sensor.pm25 = pmData.data[0].PM25;
        this.pm_sensor.pm10 = pmData.data[0].PM10;
        this.updateTime = new Date().toLocaleString('sv');
      }).catch(e => {
        console.log(e);
      });
    }
  },
  watch: {},
  mounted() {
    /* 更新溫溼度折線圖 */
    clearInterval(this.getDataInterval);
    this.getTempRhData();
    this.getDataInterval = setInterval(this.getTempRhData, 300 * 1000);
  },
  beforeUnmount() {
    clearInterval(this.getDataInterval);
  }
};
</script>

