<template>
  <div class="SmartChickenCoop">
    <Header :inputTitle="title" class="bg-2-dark-header"></Header>
    <!-- 溫濕光感測器1 -->
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="grid-content bg-2-dark" style="height:40px; line-height: 40px;">{{ air_sensor_1.title }}</div>
      </el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :span="4">
        <div class="grid-content bg-2-dark">溫度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-2-light"
          :style="[air_sensor_1.temp > 40 ? alert_high : air_sensor_1.temp < 10 ? alert_low : normal]">
          {{ air_sensor_1.temp }}<span class="unit">°C</span>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="grid-content bg-2-dark">濕度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-2-light"
          :style="[air_sensor_1.humd < 40 ? alert_high : air_sensor_1.humd > 90 ? alert_low : normal]">
          {{ air_sensor_1.humd }}<span class="unit">%RH</span>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :span="8">
        <div class="grid-content bg-2-dark">光度</div>
      </el-col>
      <el-col :span="16">
        <div class="grid-content bg-2-light">
          {{ air_sensor_1.lux }}<span class="unit">lux</span>
        </div>
      </el-col>
    </el-row>
    <!-- 溫濕光感測器2 -->
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="grid-content bg-2-dark" style="height:40px; line-height: 40px;">{{ air_sensor_2.title }}</div>
      </el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :span="4">
        <div class="grid-content bg-2-dark">溫度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-2-light"
          :style="[air_sensor_2.temp > 40 ? alert_high : air_sensor_2.temp < 10 ? alert_low : normal]">
          {{ air_sensor_2.temp }}<span class="unit">°C</span>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="grid-content bg-2-dark">濕度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-2-light"
          :style="[air_sensor_2.humd < 40 ? alert_high : air_sensor_2.humd > 90 ? alert_low : normal]">
          {{ air_sensor_2.humd }}<span class="unit">%RH</span>
        </div>
      </el-col>

    </el-row>
    <el-row :gutter="10">
      <el-col :span="8">
        <div class="grid-content bg-2-dark">光度</div>
      </el-col>
      <el-col :span="16">
        <div class="grid-content bg-2-light">
          {{ air_sensor_2.lux }}<span class="unit">lux</span>
        </div>
      </el-col>
    </el-row>

    <!-- 更新時間 -->
    <el-row>
      <el-col style="font-size:0.8rem !important; height:10px; line-height:10px; text-align:right;">
        更新時間:{{ updateTime }}
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Smart_Air_Sensor } from "../api";
import Header from "@/components/MainHeader.vue";

export default {
  components: {
    Header
  },
  data() {
    return {
      alert_high: {
        color: 'red'
      },
      alert_low: {
        color: 'blue'
      },
      normal: {
        color: 'black'
      },
      title: "智慧雞舍",
      air_sensor_1: {
        title: '溫濕光1',
        temp: 0,
        humd: 0,
        lux: 0
      },
      air_sensor_2: {
        title: '溫濕光2',
        temp: 0,
        humd: 0,
        lux: 0
      },
      getDataInterval: null,
      updateTime: "-"
    };
  },
  computed: {},
  methods: {
    getTempRhData() {
      Smart_Air_Sensor('雞舍')
        .then(response => {
          this.air_sensor_1.temp = response.data[0].TEMP;
          this.air_sensor_1.humd = response.data[0].HUMD;
          this.air_sensor_1.lux = response.data[0].LUX;
          this.air_sensor_2.temp = response.data[1].TEMP;
          this.air_sensor_2.humd = response.data[1].HUMD;
          this.air_sensor_2.lux = response.data[1].LUX;
          this.updateTime = new Date().toLocaleString('sv');
        })
        .catch(e => {
          console.log(e);
        });
    },
  },
  mounted() {
    clearInterval(this.getDataInterval);
    this.getTempRhData();
    this.getDataInterval = setInterval(this.getTempRhData, 300 * 1000);
  },
  beforeUnmount() {
    clearInterval(this.getDataInterval);
  }
};
</script>

<style scoped src="@/assets/css/compenents.css"></style>
