      <el-col :span="24">
        <div class="grid-content bg-4-dark" style="height:40px; line-height: 40px;">{{ air_sensor_1.title }}</div>
      </el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :span="4">
        <div class="grid-content bg-4-dark">溫度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-4-light"
          :style="[air_sensor_1.temp > 40 ? alert_high : air_sensor_1.temp < 10 ? alert_low : normal]">
          {{ air_sensor_1.temp }}<span class="unit">°C</span>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="grid-content bg-4-dark">濕度</div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-4-light"
          :style="[air_sensor_1.humd < 40 ? alert_high : air_sensor_1.humd > 90 ? alert_low : normal]">
          {{ air_sensor_1.humd }}<span class="unit">%RH</span>
        </div>
      </el-col>

    </el-row>
    <el-row :gutter="10">
      <el-col :span="8">
        <div class="grid-content bg-4-dark">光度</div>
