      <el-col :span="16">
        <div class="grid-content bg-4-light">
          {{ air_sensor_3.lux }}<span class="unit">lux</span>
        </div>
      </el-col>
    </el-row>
    <!-- 更新時間 -->
    <el-row>
      <el-col style="font-size:0.8rem !important; height:10px; line-height:10px; text-align:right;">
        更新時間:{{ updateTime }}
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Smart_Air_Sensor } from "../api";
import Header from "@/components/MainHeader.vue";
export default {
  components: {
    Header
  },
  data() {
    return {
      alert_high: {
        color: 'red'
      },
      alert_low: {
        color: 'blue'
      },
      normal: {
        color: 'black'
      },
      title: "黑水虻溫室",
      air_sensor_1: {
        title: '溫濕光1',
        temp: 0,
        humd: 0,
        lux: 0
      },
      air_sensor_2: {
        title: '溫濕光2',
        temp: 0,
        humd: 0,
        lux: 0
      },
      air_sensor_3: {
        title: '溫濕光3',
        temp: 0,
        humd: 0,
        lux: 0
      },
      getDataInterval: null,
      updateTime: "-"
    };
  },
  computed: {},
  methods: {
    getTempRhData() {
      Smart_Air_Sensor('蟲屋').then(response => {
        this.air_sensor_1.temp = response.data[0].TEMP;
        this.air_sensor_1.humd = response.data[0].HUMD;
        this.air_sensor_1.lux = response.data[0].LUX;
        this.air_sensor_2.temp = response.data[1].TEMP;
        this.air_sensor_2.humd = response.data[1].HUMD;
        this.air_sensor_2.lux = response.data[1].LUX;
        this.air_sensor_3.temp = response.data[2].TEMP;
        this.air_sensor_3.humd = response.data[2].HUMD;
        this.air_sensor_3.lux = response.data[2].LUX;
        this.updateTime = new Date().toLocaleString('sv');
      }).catch(e => {
        console.log(e);
      });
    }
  },
  watch: {},
  mounted() {
    /* 更新溫溼度折線圖 */
    clearInterval(this.getDataInterval);
    this.getTempRhData();
    this.getDataInterval = setInterval(this.getTempRhData, 300 * 1000);
  },
  beforeUnmount() {
    clearInterval(this.getDataInterval);
  }
};
</script>

<style scoped src="@/assets/css/compenents.css"></style>
